import empty from '@/components/empty.vue'
import cinemaSection from '@/components/cinemaSection.vue'
export default {
	name:'searchPage',
	components:{
		empty,
		cinemaSection
	},
	data() {
		return {
			value: '',
			stype: '',
			placeholder: '',
			movies: [],
			cinemas: [],
			isEmpty:true
		};
	},
	onLoad(query) {
		this.initPage(query)
	},
	watch:{
		value:function(val){
			let value = val
			this.$request(`/ajax/search?kw=${value}&cityId=57&stype=${this.stype}`).then(res => {
				let movies = res[1].data.movies ? res.data[1].movies.list : []
				movies = movies.map(item => {
					item.img = item.img.replace('w.h', '128.180')
					return item
				})
				this.movies = movies
				this.cinemas = res[1].data.cinemas ? res[1].data.cinemas.list : []
				this.isEmpty = this.movies.length || this.cinemas.length || !this.value
			})
			
		}
	},
	methods: {
		initPage(query) {
			//搜索类型，-1代表搜索影院或电影，2代表搜索影院
			const stype = query.stype
			let placeholder = ''
			if (stype === '-1') {
				placeholder = '搜电影、搜影院'
			} else {
				placeholder = '搜影院'
			}
			this.stype = stype
			this.placeholder = placeholder
		},
		goBack() {
			uni.navigateBack({
				delta: 1
			})
		}
	}

}
